@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Roboto:wght@300&display=swap');


.contact-hero {
    width: 100%;
    height: 80vh;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background: #0d0d0e7a;
  }
  
  .contact-hero:before {
    content: '';
    position: absolute;
    background: url('../../assets/menu-bg-contact.jpg') no-repeat center center/cover;
    height: 100%;
    width: 100%;
    z-index: -1;
  }
  
.contact-hero .contact-hero-content {
    display: block;
    position: absolute;
    top: 30%;
    padding: 1rem;
}

.contact-hero .contact-hero-content h1 {
    font-size: 4rem;
    font-family: 'Montserrat', sans-serif;
    color: white;
    text-align: center;
}

.contact-hero .contact-hero-content h2 {
    color: white;
    font-size: 1.5rem;
    padding: 1rem 0;
    letter-spacing: 3px;
    text-align: center;
}

.contact-hero .contact-hero-content p {
    font-family: 'Montserrat', sans-serif;
    color: white;
    text-align: center;

}


/* MEDIA QUERIES */

@media screen and (max-width:940px) {
    .contact-hero .contact-hero-content h1 {
        font-size: 3rem;
    }
    
    .contact-hero .contact-hero-content h2 {
        font-size: 1.5rem;
    }

}